.button-fix {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.text-decor-none {
    text-decoration: none;
}

.photo-shell {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.photo-post {
    width: 45%;
    border-radius: 15px;
    margin-bottom: 20px;
}


.fix-margin-icon {
    margin-top: 16px;
    color: #99a2ad;
}


.photo-preview {
    width: 97%;
    margin: 10px;
}
